<template>
	<view class="wrapper padding">
		<view class="info">
			<view class="add" @click="add">
				<text>创建订单</text>
				<image src="../../static/add.png"></image>
			</view>
		</view>
		<view class="list"  v-if="!empty">
			<view class="orderList">
				<view 
					v-for="(litt,idx) in listData" 
					:key="idx"
					class='order-wrapper'
				>	
					<view class="order_item rowo">
						<view class='txt desc'>意向车型</view>
						<view class='txt want'>{{litt.carTypeName}}</view>
						<view class='status'>
							<text class='shape'></text>
							<text class='txt'>{{litt.nodeStepName}}</text>
							<text class='cancel-order flex-row-center' v-if="litt.orderState==-1">订单已取消</text>
						</view>
					</view>
					<view class="order_item">
						<view class='txt desc t'>创建时间:</view>
						<view class='txt  t'>{{litt.createTimeStamp?litt.createTimeStamp:''}}</view>
					</view>
					<view class="order_item">
						<view class='txt desc t'>最近修改时间:</view>
						<view class='txt  t'>{{litt.lastModifyTimeStamp?litt.lastModifyTimeStamp:''}}</view>
					</view>
					<view class="order_item flex-row-end">
						<button class='cu-btn control_btn bg-white' @click="goEdit(litt)"
							v-if="litt.orderState!=-1 && litt.orderState<5"
						>编辑订单</button>
						<button class='cu-btn control_btn bg-white' @click="goDetail(litt)">购车进度维护</button>
					</view>
				</view>
			</view>
			<loadMore :status="status" :content-text="textDesc"></loadMore>
		</view>
		<view class="no_data" v-else>
			暂无数据
		</view>
	</view>
</template>
<script>
	import loadMore from "../../components/loadMore/loadMore.vue"
	import {formatTime} from "../../common/tools.js"
	export default {
		data() {
			return {
				textDesc:{contentdown: "加载更多",contentrefresh: "正在加载...",contentnomore: "没有更多数据了"},
				status:"noMore", //可选值：more（loading前）、loading（loading中）、noMore（没有更多了
				empty:false,
				listData:[],  //分页
				userInfo:{}
			};
		},
		methods:{
			goDetail(litt){
				this.perfectNavigateTo({
					url:`/pages/client/timeline?userId=${this.userInfo.userId}&orderInfo=${JSON.stringify(litt)}`
				})
			},
			add(){
				if(this.userInfo.isBind && this.userInfo.isBind==1){
					this.perfectNavigateTo({
						url:`/pages/staff/editOrder?fromType=3`
					})
				}else{
					uni.showToast({title:'请和员工绑定后创建订单',icon:'none'})
				}
			},
			goEdit(item){
				this.perfectNavigateTo({
					url:`/pages/staff/editOrder?orderInfo=${JSON.stringify(item)}&fromType=1`
				})
			},
			getList(){ // 获取所有订单
				this.request("/order/member/orders",{memberId:this.userInfo.userId}).then((res)=>{
					if(res.resultCode==0){
						let _data=res.data.orderList
						if(_data.length>0){ //如果有数据
							this.empty=false
							_data.forEach((item,index)=>{
								item.createTimeStamp=formatTime(item.createTimeStamp)
								item.lastModifyTimeStamp=formatTime(item.lastModifyTimeStamp)
							})
							this.listData=_data
						}else{ //如果这次没数据
							this.empty=true
						}
						this.status="noMore"
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
					uni.stopPullDownRefresh()
				})
			},
			tel(tel){
				uni.showModal({
					title:"提示",
					content:"是否确认拨打电话",
					success:(res)=>{
						if(res.confirm){
							uni.makePhoneCall({
							  phoneNumber:tel //仅为示例，并非真实的电话号码
							})
						}
					}
				})
			},
			getUserInfo(){
				this.request("/user/info").then((res)=>{
					if(res.resultCode==0){
						this.userInfo=res.data
						this.getList()
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			}
		},
		components:{
			loadMore
		},
		onShareAppMessage(){
			return {
				title:'卫蓝购',//String	是	分享标题	
				path:'/pages/infomation/index',//String	是	页面 path ，必须是以 / 开头的完整路径。	QQ小程序不支持
				imageUrl:"/static/share.png"
			}
		},
		onPullDownRefresh(){ //下拉刷新  无分页
			this.listData=[]
			this.getUserInfo()
		},
		onReachBottom(){ //加载更多
			if(this.status!="noMore"){
				this.getList()
			}
		},
		onShow(){
			uni.startPullDownRefresh();
		},
	}
</script>
<style lang="stylus" scoped>
	.wrapper
		.info
			display:flex
			flex-direction row
			align-items center
			.add
				height:112upx;
				width:690upx
				margin: 0 auto
				display:flex;
				flex-direction:row;
				align-items center;
				background:rgba(248,249,252,1);
				border-radius:19upx;
				padding:0upx 30upx
				justify-content space-between
				margin-top:30upx
				text
					font-size:32upx;
					font-family:PingFangSC-Semibold,PingFang SC;
					font-weight:600;
					color:rgba(30,30,30,1);
				image
					width:28upx
					height:28upx
		.list
			.orderList
				// margin-left:143upx
				margin-top:30upx
				.order-wrapper
					background:rgba(248,249,252,1);
					border-radius:8upx
					padding:0upx 20upx 20upx 20upx
					&:not(:first-child)
						margin-top:20upx
					.order_item
						display:flex
						margin-top:16upx
						flex-direction row
						align-items center
						&.rowo
							height:82upx;
							border-bottom:2upx solid #E3E9F0
							margin-top:0upx
						.txt
							color:#000000
							font-size:24upx
							&.t
								font-size:24upx;
								font-family:PingFangSC-Regular,PingFang SC;
								font-weight:400;
								color:rgba(100,100,100,1);
							&.desc
								margin-right:20upx
							&.want
								flex-basis:200upx
								text-overflow ellipsis;
								overflow hidden
								white-space:nowrap
						image
							width:26upx
							height:26upx
						.status
							display:flex
							flex-direction row
							align-items center
							flex:1
							justify-content flex-end
							.shape
								width:8upx;
								height:8upx;
								transform rotate(45deg)
								background:linear-gradient(135deg,rgba(44,143,255,1) 0%,rgba(23,108,221,1) 100%);
								border-radius:2upx;
							.txt
								color:#0075D6
								font-size:24upx
								margin:0upx 10upx
							.cancel-order
								background:rgba(225,121,105,1);
								border-radius:4upx;
								padding:0upx 8upx
								height:34upx
								color:#fff
								font-size:24upx
					.control_btn
						margin-left:20upx
						color:#646464
						border:1upx solid rgba(100,100,100,1);
</style>			
	
